import React, { useState,useReducer,useEffect } from 'react'
import { ShimmerPostList } from "react-shimmer-effects";

const baseURL = 'https://api.nytimes.com/svc/search/v2/articlesearch.json';
const key = 'omWzfc2BGpS20PfbCTWB6eaVUOGbF1qd';

const reducer=(state,action)=>{
    switch(action.type){
        case "set":
            return action.data;
        default:
            return state;
    }
}

function ResultBox({inputdata}){
    const [articles,dispatch]=useReducer(reducer,{})
    const [isLoading,setisLoading]=useState(-1);
    const [page,setpage]=useState(0);

    const setApiDataAndisLoading=async (jsondata)=>{
        await dispatch({type:"set",data:jsondata.response.docs});
        await setisLoading(0);
    }

    useEffect(()=>{
        if(inputdata.searchkey!==""){
            setisLoading(1);
            let url=`${baseURL}?api-key=${key}&page=${page}&q=${inputdata.searchkey}&fq=document_type:("article")`;
            if(inputdata.startDate!=="")
                url+=`&begin_date=${inputdata.startDate}&end_date=${inputdata.endDate}`;

            fetch(url).then((response)=>{
                return response.json();
            }).then((jsondata)=>{
                setApiDataAndisLoading(jsondata);
            }).catch((errmsg)=>{
                console.error(errmsg);
            })
        }
    },[inputdata,page])
    useEffect(()=>{
        setpage(0);
    },[inputdata])

    const Articles = Object.entries(articles);

    const increment=()=>{
        setpage(page+1);
    }

    const decrement=()=>{
        if(page!==0)
            setpage(page-1);
    }

    let jsxobj=(
        <div className="result">
            <span>Showing results for "{inputdata.searchkey}"</span>
                <div className="card-deck">
                    {
                        Articles.map((x, j) => {

                            const article = x[1];
                            console.log(article);
                            if(article.multimedia.length > 0){
                                return (
                                    <div className="card" key={j}>
                                    <img className="card-img-top" src={`https://www.nytimes.com/${article.multimedia[0].url}`} alt="Error Loading" />
                                    <div className="card-body">
                                        <h5 className="card-title"><a href={article.web_url} target="_blank" rel='noreferrer'>{article.headline.main}</a></h5>
                                        <p className="card-text">{article.abstract}</p>
                                    </div>
                                </div>)
                            }
                            else
                                return (<></>)
                        })
                    }
                </div>
                
                <div className='next-prev-btn'>
                    <button className='btn-main btn btn-primary' onClick={decrement}>Previous</button>
                    <h4>{page+1}</h4>
                    <button className='btn-main btn btn-primary' onClick={increment}>Next</button>
                </div>
        </div>
    )


    if (isLoading === -1)
        return (<></>);
    else if(isLoading)
        return (<div>
        <span>Please wait while we get your Articles</span>   
        <ShimmerPostList postStyle="STYLE_FOUR" col={1} row={1} />
        </div>
        );
    else
        return jsxobj;
}

export default React.memo(ResultBox);
